import React from "react";

const EventDetails = ({ event }) => {
  const fDate = new Date(event.date);
  const fTime = new Date("1970-01-01T" + event.time + "Z").toLocaleTimeString(
    {},
    { timeZone: "UTC", hour12: true, hour: "numeric", minute: "numeric" }
  );
  return (
    <div className="EventDetails">
      <h2 style={{ textAlign: "center" }}>{event.name}</h2>
      <div className="eventDetailsImage">
        <img src={event.image} alt={event.name} />
      </div>
      <p>{event.description}</p>
      <ul>
        <li>
          <b>When:</b> {fDate.toLocaleDateString("en-US")} at {fTime}
        </li>
        {event.address ? (
          <li>
            <b>Where:</b> {event.address}
          </li>
        ) : (
          ""
        )}
        <li>
          <b>
            Color:{" "}
            <span style={{ color: event.color, textTransform: "uppercase" }}>
              {event.color}
            </span>
          </b>
        </li>
        <li>
          <b>Email:</b> {event.email}
        </li>
        {event.phone ? (
          <li>
            <b>Phone:</b> {event.phone}
          </li>
        ) : (
          ""
        )}
      </ul>
    </div>
  );
};

export default EventDetails;
