import React from "react";
import EventListItem from "./EventListItem";

const EventList = ({ events }) => {
  const eventsWithImg = events.map((i) => {
    return !i.image
      ? {
          ...i,
          image: "https://source.unsplash.com/random/320x240/?" + i.color
        }
      : i;
  });
  //console.log("Original", events);
  const sorted = eventsWithImg
    .slice()
    .sort((a, b) => a.name.localeCompare(b.name));
  //console.log("Sorted", sorted);
  const renderedList = sorted.map((event) => {
    return <EventListItem key={event.id} event={event} />;
  });

  return <div className="EventList">{renderedList}</div>;
};

export default EventList;
