import React from "react";

function Header() {
  return (
    <div className="Header">
      <div className="buttons">
        <button
          className="btn btn-outline-primary"
          data-bs-toggle="modal"
          data-bs-target="#addNewEvent"
        >
          Add New Event
        </button>
      </div>
    </div>
  );
}

export default Header;
