import React from "react";
import axios from "axios";
import EventDetailsModal from "../Components/EventDetailsModal";
import EditEventModal from "../Components/EditEventModal";

const EventListItem = ({ event }) => {
  const handleDelete = (event) => {
    if (window.confirm("Are you sure want to delete " + event.name + "?")) {
      deleteEvent(event.id);
    } else {
      console.log("Delete cancelled.");
    }
  };

  const deleteEvent = async (id) => {
    try {
      const response = await axios.delete(
        "https://rf-json-server.herokuapp.com/events/" + id
      );
      window.location.reload();
    } catch (error) {
      alert("Unable to delete event! :(\nSee console for details.");
      console.log(error);
    }
  };

  return (
    <div className="EventListItem" key={event.id}>
      <div className="card mb-3">
        <div className="row g-0">
          <div className="col-md-4">
            <img
              src={
                event.image
                  ? event.image
                  : "https://source.unsplash.com/random/320x240"
              }
              className="img-fluid rounded-start"
              alt={event.name}
            />
          </div>
          <div className="col-md-8">
            <div className="card-body">
              <h5 className="card-title">{event.name}</h5>
              <div className="card-text">
                <p className="desc">{event.description}</p>
                <p className="date">By {event.company}</p>
              </div>
              <div className="card-btns">
                <button
                  className="btn btn-outline-primary"
                  data-bs-toggle="modal"
                  data-bs-target={"#editEvent" + event.id}
                >
                  Edit
                </button>
                <button
                  className="btn btn-outline-primary"
                  onClick={() => handleDelete(event)}
                >
                  Delete
                </button>
                <button
                  className="btn btn-primary"
                  data-bs-toggle="modal"
                  data-bs-target={"#eventDetails" + event.id}
                >
                  Event Details
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <EventDetailsModal event={event} />
      <EditEventModal event={event} />
    </div>
  );
};

export default EventListItem;
