import { useState, useEffect } from "react";
import getEvents from "../API/getEvents";

const useEvents = (eventId) => {
  const [events, setEvents] = useState([]);

  useEffect(() => {
    search(eventId);
  }, [eventId]);

  const search = async (eventId) => {
    try {
      const resp = await getEvents.get(eventId, {});
      setEvents(resp.data);
    } catch (error) {
      alert("Unable to get events! :(\nSee console for details.");
      console.log(error);
    }
  };
  return [events, search];
};

export default useEvents;
