import React from "react";
import axios from "axios";

function NewEvent() {
  const onFormSubmit = async (e) => {
    e.preventDefault();
    const formData = {
      color: e.target.eventColor.value,
      name: e.target.eventName.value,
      date: e.target.eventDate.value,
      time: e.target.eventTime.value,
      email: e.target.eventEmail.value,
      company: e.target.eventCompany.value,
      description: e.target.eventDesc.value
    };

    try {
      axios.post("https://rf-json-server.herokuapp.com/events", formData);
      window.location.reload();
    } catch (error) {
      alert("Unable to create new event! :(\nSee console for details.");
      console.log(error);
    }
  };

  return (
    <div className="NewEvent">
      <form className="row g-3" id="new-event-form" onSubmit={onFormSubmit}>
        <div className="col-md-6">
          <label htmlFor="eventCompany" className="form-label">
            Company
          </label>
          <input
            type="text"
            className="form-control"
            id="eventCompany"
            required="required"
            name="eventCompany"
          />
        </div>
        <div className="col-md-6">
          <label htmlFor="eventColor" className="form-label">
            Color
          </label>
          <select id="eventColor" name="eventColor" className="form-select">
            <option defaultValue>Choose...</option>
            <option value="red">Red</option>
            <option value="blue">Blue</option>
            <option value="green">Green</option>
            <option value="orange">Orange</option>
            <option value="pink">Pink</option>
          </select>
        </div>
        <div className="col-12">
          <label htmlFor="eventName" className="form-label">
            Event Name
          </label>
          <input
            type="text"
            className="form-control"
            id="eventName"
            name="eventName"
            placeholder=""
            required="required"
          />
        </div>
        <div className="col-12">
          <label htmlFor="eventDesc" className="form-label">
            Event Description
          </label>
          <input
            type="text"
            className="form-control"
            id="eventDesc"
            name="eventDesc"
            placeholder=""
            required="required"
          />
        </div>
        <div className="col-md-6">
          <label htmlFor="eventDate" className="form-label">
            Date
          </label>
          <input
            type="date"
            className="form-control"
            id="eventDate"
            name="eventDate"
            required="required"
          />
        </div>
        <div className="col-md-6">
          <label htmlFor="eventTime" className="form-label">
            Time
          </label>
          <input
            type="time"
            className="form-control"
            id="eventTime"
            name="eventTime"
            required="required"
          />
        </div>
        <div className="col-12">
          <label htmlFor="eventEmail" className="form-label">
            Event Email
          </label>
          <input
            type="email"
            className="form-control"
            id="eventEmail"
            name="eventEmail"
            required="required"
          />
        </div>
        <button type="submit" className="btn btn-primary">
          Submit
        </button>
      </form>
    </div>
  );
}

export default NewEvent;
