import React from "react";
import axios from "axios";

function EditEvent({ event }) {
  const onFormSubmit = async (e) => {
    e.preventDefault();
    const formData = {
      color: "",
      name: "",
      date: "",
      time: "",
      email: "",
      company: "",
      description: ""
    };

    var data = e.target;
    for (var i = 0; i < data.length; i++) {
      if (data[i].name.toLowerCase().indexOf("color") > -1) {
        formData.color = data[i].value;
      } else if (data[i].name.toLowerCase().indexOf("name") > -1) {
        formData.name = data[i].value;
      } else if (data[i].name.toLowerCase().indexOf("date") > -1) {
        formData.date = data[i].value;
      } else if (data[i].name.toLowerCase().indexOf("time") > -1) {
        formData.time = data[i].value;
      } else if (data[i].name.toLowerCase().indexOf("email") > -1) {
        formData.email = data[i].value;
      } else if (data[i].name.toLowerCase().indexOf("company") > -1) {
        formData.company = data[i].value;
      } else if (data[i].name.toLowerCase().indexOf("desc") > -1) {
        formData.description = data[i].value;
      }
    }

    var url = "https://rf-json-server.herokuapp.com/events/" + event.id;
    try {
      axios.put(url, formData);
      window.location.reload();
    } catch (error) {
      alert("Unable to edit event! :(\nSee console for details.");
      console.log(error);
    }
  };

  return (
    <div className="EditEvent">
      <form
        className="row g-3"
        id={"edit-event-form" + event.id}
        onSubmit={onFormSubmit}
      >
        <div className="col-md-6">
          <label htmlFor={"editEventCompany" + event.id} className="form-label">
            Company
          </label>
          <input
            defaultValue={event.company}
            type="text"
            className="form-control"
            id={"editEventCompany" + event.id}
            required="required"
            name={"editEventCompany" + event.id}
          />
        </div>
        <div className="col-md-6">
          <label htmlFor={"editEventColor" + event.id} className="form-label">
            Color
          </label>
          <select
            id={"editEventColor" + event.id}
            name={"editEventColor" + event.id}
            className="form-select"
          >
            <option defaultValue={event.color}>{event.color}</option>
            <option value="red">Red</option>
            <option value="blue">Blue</option>
            <option value="green">Green</option>
            <option value="orange">Orange</option>
            <option value="pink">Pink</option>
          </select>
        </div>
        <div className="col-12">
          <label htmlFor={"editEventName" + event.id} className="form-label">
            Event Name
          </label>
          <input
            defaultValue={event.name}
            type="text"
            className="form-control"
            id={"editEventName" + event.id}
            name={"editEventName" + event.id}
            placeholder=""
            required="required"
          />
        </div>
        <div className="col-12">
          <label htmlFor={"editEventDesc" + event.id} className="form-label">
            Event Description
          </label>
          <input
            defaultValue={event.description}
            type="text"
            className="form-control"
            id={"editEventDesc" + event.id}
            name={"editEventDesc" + event.id}
            placeholder=""
            required="required"
          />
        </div>
        <div className="col-md-6">
          <label htmlFor={"editEventDate" + event.id} className="form-label">
            Date
          </label>
          <input
            defaultValue={event.date}
            type="date"
            className="form-control"
            id={"editEventDate" + event.id}
            name={"editEventDate" + event.id}
            required="required"
          />
        </div>
        <div className="col-md-6">
          <label htmlFor={"editEventTime" + event.id} className="form-label">
            Time
          </label>
          <input
            defaultValue={event.time}
            type="time"
            className="form-control"
            id={"editEventTime" + event.id}
            name={"editEventTime" + event.id}
            required="required"
          />
        </div>
        <div className="col-12">
          <label htmlFor={"editEventEmail" + event.id} className="form-label">
            Event Email
          </label>
          <input
            defaultValue={event.email}
            type="email"
            className="form-control"
            id={"editEventEmail" + event.id}
            name={"editEventEmail" + event.id}
            required="required"
          />
        </div>
        <button type="submit" className="btn btn-primary">
          Submit
        </button>
      </form>
    </div>
  );
}

export default EditEvent;
